.gpt3__blog-container_article {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: var(--color-footer);
  font-family: var(--font-family);
}
.gpt3__blog-container_article-image {
  width: 100%;
  height: 100%;
  background: var(--color-footer);
}
.gpt3__blog-container_article-image img {
  width: 100%;
  height: 100%;
}
.gpt3__blog-container_article-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  height: 100%;
}
.gpt3__blog-container_article-content p {
  font-weight: 700;
  font-size: 12px;
  line-height: 35px;
  color: #ffffff;
}
.gpt3__blog-container_article-content h3 {
  font-weight: 800;
  font-size: 26px;
  line-height: 30px;
  color: #ffffff;
  margin-bottom: 5rem;
  cursor: pointer;
}
.gpt3__blog-container_article p:last-child {
  font-weight: 700;
  font-size: 11.649px;
  line-height: 35px;
  color: #ffffff;
  cursor: pointer;
}
@media screen and (max-width:500px) {
    .gpt3__blog-container_article-content h3{
        font-size: 18px;
        line-height: 25px;
    }
}